@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500&display=swap);
:root {
  --primary: #fe043c;
  --secondary: #fff5ec;
  --secondaryDeep: #ffedde;
  --textMain: #2e266f;
  --textSecondary: #707070;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: #fff5ec;
  background-color: var(--secondary);
}

.row {
  width: 100%;
  display: flex;
}

.centerInParent {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: left;
}

.bannar-wrapper {
  padding: 4rem 5rem;
}

.bannar-row {
  grid-gap: 20px;
  gap: 20px;
  width: 80%;
  margin: 0 auto;
}
.bannar-wrapper .left {
  border-left: 8px solid var(--primary);
  padding-left: 1rem;
}
.bannar-wrapper .left h1 {
  color: var(--textMain);
}

.bannar-wrapper .right p {
  margin: 0;
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  .bannar-wrapper {
    padding: 1rem 2rem;
  }
  .bannar-row {
    flex-direction: column;
    width: 100%;
  }
}

.booking-Wrapper {
  height: 500px;
  display: flex;
  align-items: center;
  position: relative;
  padding: 6rem;

  /* background-image: url("../../images/lattuce.png");
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-size: contain;
  border: 1px solid gray; */
}

