.border {
  width: 100px;
  height: 10px;
  background-color: var(--primary);
}

.booking-area {
  width: 80%;
  height: 350px;
  margin: 0 auto;
  background-color: white;
  padding: 4rem;
  border-radius: 5px;
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

.booking-area h2 {
  font-size: 2rem;
  color: var(--textMain);
}

.booking-area::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(/cookfood/static/media/bg-overlay.d9cf01f6.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-size: 20%;
  left: -30px;
  bottom: 0;
  z-index: -1;
}

.booking-area::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: greenyellow; */
  background-image: url(/cookfood/static/media/bg-overlay.d9cf01f6.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-x: 100%;
  background-size: 20%;
  right: -30px;
  top: -30px;
  z-index: -1;
}

.booking-form {
  width: 70%;
  height: 60px;
  font-size: 1rem;
  background-color: #fff;
  display: flex;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0 2px 30px 0px rgba(255, 112, 112, 16%);
}

.booking-form button {
  outline: none;
  border: none;
  height: 100%;
  width: 30%;
  color: #fff;
  font-weight: 700;
  background-color: var(--primary);
  border-top-left-radius: 100px;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  transition: -webkit-filter 0.1s ease-in-out;
  transition: filter 0.1s ease-in-out;
  transition: filter 0.1s ease-in-out, -webkit-filter 0.1s ease-in-out;
}

.booking-form input {
  outline: none;
  border: none;
  height: 100%;
  width: 100%;
  padding-left: 1rem;
  font-size: 1rem;
}

.booking-Wrapper img {
  position: absolute;
  width: 500px;
  z-index: 3;
  left: -150px;
}

/* Root */
.exploreFoodWrapper {
  width: 80%;
  margin: 0 auto;
}

.exploreFoodWrapper h3 {
  font-size: 2rem;
  color: var(--textMain);
  text-align: center;
}
.exploreFoodWrapper p {
  text-align: center;
  color: var(--textSecondary);
  width: 50%;
  margin: 1rem auto;
}

.foodItemsRow {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 20rem);
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

/* Card */
.cardExplorar-wrapper {
  background-color: #fff;
  /* width: 29%; */
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: border-bottom 0.4s ease-in-out;
}

.cardExplorar-wrapper img {
  width: 100%;
}

.cardExplorar-wrapper .nameAndPrice {
  display: flex;
  justify-content: space-between;
  padding: 10px 1rem;
  padding-bottom: 0;
}

.cardExplorar-wrapper .nameAndPrice h3 {
  font-size: 1rem;
  font-weight: 500;
}

.cardExplorar-wrapper .nameAndPrice .price {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--primary);
}

.cardExplorar-wrapper p {
  width: 100%;
  padding: 10px 1rem;
  padding-top: 0;
  text-align: justify;
  box-sizing: border-box;
  font-size: 15px;
  color: var(--textSecondary);
}

footer {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

footer::before {
  content: "";
  left: 0;
  bottom: 0;
  width: 90%;
  height: 110%;
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(/cookfood/static/media/footer.9a5c00e5.png);
}

footer h2 {
  font-weight: 600;
  color: var(--textMain);
}

footer p {
  margin: 1rem auto;
  color: var(--textSecondary);
}

footer .newsletterEmail {
  background: #fff;
  padding: 5px;
  border-radius: 50px;
  padding-left: 10px;
  margin-top: 2rem;
}

footer .newsletterEmail input {
  outline: none;
  border: none;
  padding: 0.4rem 0.6rem;
  width: 300px;
}
footer .newsletterEmail button {
  height: 50px;
  width: 50px;
  outline: none;
  border: none;
  background: var(--primary);
  padding: 0.8rem;
  border-radius: 100%;
  color: #fff;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

footer .newsletterEmail button:hover {
  background: var(--textMain) 50%;
}

footer ul {
  list-style: none;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  margin-top: 2rem;
}

footer ul li {
  color: var(--textSecondary);
}

main {
  height: 100%;
  padding: 2rem;
  position: relative;
}

main::after {
  content: "";
  height: 100%;
  width: 25%;
  background-color: var(--primary);
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

main::before {
  content: "";
  height: 80%;
  width: 80%;
  background-image: url(/cookfood/static/media/bg-overlay.d9cf01f6.png);
  background-repeat: no-repeat;
  background-position: 100% 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.hero-root > div {
  flex-grow: 1;
  width: 50%;
}

/* Hero Left */
.hero-left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 4rem;
}

.hero-left .ovarlayText {
  font-size: 8rem !important;
  position: absolute;
  top: 10%;
  z-index: -2;
  opacity: 0.1;
}
.hero-left h1 {
  font-size: 50px;
  color: var(--textMain);
}

.hero-left .hero-search {
  width: 70%;
  height: 60px;
  font-size: 1rem;
  background-color: #fff;
  display: flex;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0 3px 50px 0px rgba(255, 112, 112, 16%);
}

.hero-left .hero-search button {
  outline: none;
  border: none;
  height: 100%;
  width: 30%;
  color: #fff;
  font-weight: 700;
  background-color: var(--primary);
  border-top-left-radius: 100px;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  transition: -webkit-filter 0.1s ease-in-out;
  transition: filter 0.1s ease-in-out;
  transition: filter 0.1s ease-in-out, -webkit-filter 0.1s ease-in-out;
}
.hero-left .hero-search button:focus {
  opacity: 0.9;
  -webkit-filter: drop-shadow(0px 0px 10px #ff3d6847);
          filter: drop-shadow(0px 0px 10px #ff3d6847);
}

.hero-left .hero-search button:not(:focus) {
  opacity: 1;
  -webkit-filter: drop-shadow(0px 0px 0px #fff);
          filter: drop-shadow(0px 0px 0px #fff);
}

.hero-left .hero-search input {
  outline: none;
  border: none;
  height: 100%;
  width: 100%;
  padding-left: 1rem;
  font-size: 1rem;
}

/* Hero Right */
.hero-right {
  position: relative;
}
.hero-right img {
  width: 80%;
  position: relative;
}

.hero-right::before {
  content: "";
  position: absolute;
  right: 0px;
  height: 100%;
  width: 100%;
  background-image: url(/cookfood/static/media/lattuce.65259f6a.png);
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: 60%;
}

@media only screen and (max-width: 500px) {
  main {
    height: 100vh;
    padding-bottom: 0;
  }
  main .hero-root {
    flex-direction: column-reverse !important;
  }

  main::after {
    display: none;
  }
  .hero-root > div {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    padding: 0;
  }
  .hero-left {
    margin-top: 2rem;
    padding-top: 3rem !important;
  }
  .hero-left h1 {
    font-size: 2rem;
  }
  .hero-left .ovarlayText {
    font-size: 5rem;
    top: 40%;
    text-align: center;
  }
  .hero-left .hero-search {
    width: 100%;
    margin-top: 2rem;
  }

  main::before {
    content: "";
    height: 300px;
    width: 300px;
    background-position: 100% 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
}

.mobileApp-wrapper {
  padding: 3rem;
  background-color: var(--secondaryDeep);
}

.mobileApp-row {
  width: 90%;
  margin: 0 auto;
  display: grid;
  position: relative;
  grid-template-columns: repeat(2, 1fr);
}

.mobileApp-row .mobileApp-text {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.mobileApp-row .mobileApp-text p {
  color: var(--textSecondary);
  margin-top: 0.5rem;
  padding-left: 1.5rem;
}

.mobileApp-row .mobileApp-text h1 {
  font-size: 2.5rem;
  color: var(--textMain);
  line-height: 1.2;
  border-left: 8px solid var(--primary);
  padding-left: 1rem;
}

.mobileApp-row .mobileApp-text .mobileApp-platforms img {
  width: 160px;
  margin-top: 15px;
  margin-right: 10px;
}

.appStore {
  height: 48px;
}

.mobileApp-row .mobileApp-screenshort {
  position: relative;
  text-align: center;
}

.mobileApp-row .mobileApp-screenshort img {
  width: 60%;
}

.mobileApp-row .mobileApp-screenshort .mobile-bg {
  position: relative;
  z-index: 1;
}

.mobileApp-row .mobileApp-screenshort .mobile-bg::before {
  content: "ss";
  background-color: greenyellow;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 2;
  position: absolute;
}

.mobileApp-row .mobileApp-screenshort .lotas-bg {
  position: absolute;
  left: 10px;
  top: 30%;
  z-index: 0;
}

.bgOvarlay {
  position: absolute;
  left: 10%;
  top: 10%;
  width: 40% !important;
}

/* Our Food >js */
.ourprodouct-wrapper {
  height: 550px;
  display: flex;
  align-items: center;
  background-image: url(/cookfood/static/media/bg-overlay.d9cf01f6.png);
  background-repeat: no-repeat;
  background-size: 20%;
}
.ourfood-slider {
  padding: 1rem;
  display: flex;
}

/* Food Card Stylings */
.ourfood-card {
  width: 400px;
  height: 400px;
  position: relative;
  margin: 6px;
}

.card-image {
  width: 400px;
}
.foodCard-info {
  background-color: #fe043abe;
  bottom: 0;
  color: #fff;
  position: absolute;
  padding: 1rem;
  box-sizing: border-box;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.foodCard-info h3 {
  font-size: 2rem;
}

.foodCard-info p {
  font-size: 14px;
  line-height: 1.2;
  font-weight: 300;
}

.foodcard-lower {
  margin-top: 20px;
  justify-content: space-between;
  align-items: center;
}

.foodcard-lower .cardinfo-location p {
  font-weight: 500;
  font-size: 16px;
}

.foodcard-lower .cardinfo-booknow button {
  padding: 0.4rem 1rem;
  border-radius: 4px;
  background-color: #fff;
  color: var(--primary);
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1.2;
  outline: var(--primary);
  border: var(--primary);
}

@media only screen and (max-width: 600px) {
  .ourprodouct-wrapper {
    height: auto;
    justify-content: center;
  }
  .ourfood-slider {
    flex-direction: column;
    align-items: center;
  }
}

.ourservice-wrapper {
  padding: 5rem;
}

.ourservice-row-1 {
  width: 80%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.row1-left h1 {
  font-size: 3rem;
  color: var(--textMain);
}
.row1-left {
  border-left: 10px solid var(--primary);
  padding-left: 1rem;
}

/* Services Grid */
.ourServiceGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-rows: 1fr 1fr;
  width: 80%;
  grid-gap: 30px;
  gap: 30px;
  margin: 4rem auto;
  margin-top: 5rem;
  grid-template-areas:
    " tableBookingImage tableBookingText freeHomeDelivary"
    " freeFoodText freeFoodImage freeHomeDelivary";
}

.ourServiceGrid img {
  width: 50%;
}

/* Table Booking Image - Row 1 */
.tableBookingImage {
  grid-area: tableBookingImage;
  background-color: var(--secondaryDeep);
  padding: 2rem;
}

/* Table Booking Text - Row 2 */
.tableBookingText {
  grid-area: tableBookingText;
  text-align: left;
  align-items: flex-end;
}
.tableBookingText h3 {
  position: relative;
  color: var(--textMain);
}
.tableBookingText .booking::after {
  content: "";
  top: 50%;
  left: -120%;
  width: 100%;
  height: 4px;
  position: absolute;
  border-radius: 20px;
  background-color: var(--primary);
}
.tableBookingText p {
  text-align: right;
  line-height: 1.1;
  margin-top: 5px;
  color: var(--textSecondary);
}

/* Free Home Delivary - Row 3 */
.freeHomeDelivary {
  grid-area: freeHomeDelivary;
  background-color: var(--secondaryDeep);
}

/* Free Food Text - Row 4 */
.freeFoodText {
  grid-area: freeFoodText;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.freeFoodText h3 {
  text-align: left;
  color: var(--textMain);
  position: relative;
}

.freeFoodText .freeFood {
  margin-left: 5rem;
  position: relative;
}
.freeFoodText .freeFood::after {
  content: "";
  width: 50%;
  height: 4px;
  left: -55%;
  top: 50%;
  position: absolute;
  background-color: var(--primary);
}

.freeFoodText::after {
  content: url(data:image/png;base64,);
  width: auto;
  height: auto;
  right: -76px;
  position: absolute;
}

.freeFoodText p {
  line-height: 1.1;
  margin-top: 5px;
  color: var(--textSecondary);
}

/* Free Food Image - Row 5 */
.freeFoodImage {
  grid-area: freeFoodImage;
  padding: 2rem;
  background-color: var(--secondaryDeep);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

