.bannar-wrapper {
  padding: 4rem 5rem;
}

.bannar-row {
  gap: 20px;
  width: 80%;
  margin: 0 auto;
}
.bannar-wrapper .left {
  border-left: 8px solid var(--primary);
  padding-left: 1rem;
}
.bannar-wrapper .left h1 {
  color: var(--textMain);
}

.bannar-wrapper .right p {
  margin: 0;
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  .bannar-wrapper {
    padding: 1rem 2rem;
  }
  .bannar-row {
    flex-direction: column;
    width: 100%;
  }
}
